@import '../../styles/variables.scss';

.portfolio_done, .portfolio_todo {
    background: linear-gradient(to top, $secondary-color,  $third-color );
    padding-bottom: 30px;
    & h2{
        margin : 20px auto;
        @media (min-width : 768px) {
            width: 60%;
        }
    }
    &__gallery {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 20px 10px 0 10px;
        @media (min-width : 768px) {
        flex-direction: row;
        flex-wrap : wrap;
        justify-content: space-around;
    }
    &-card {
        display :flex;
        justify-content: center;
            @media (min-width : 768px) {
            width: 45%;
        }
        & > a {
            text-decoration: none;
            }
        } 
    }
    & .title {
        width : 90%;
        @media (min-width : 768px) {
            width: 60%;
            font-size: 1.1em;
        }
    }
}


.portfolio_todo {
    background: $third-color ;
}



