@import '../../styles/variables.scss';

footer {
    display: flex;
    flex-direction: column;
    padding-top : 1em;
    align-items: center;
    width : 100%;
    height: 6em;
    background-color: $primary-color;
    & p, span{
        margin : 30px;
        color : white;
        font-size: 0.7em;
        }
        & span {
            top : 60px;
        }
}

.footer__icon {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width : 20%;
    max-width: 100px;
    & a, img {
        height: 30px;
    }
}