@import '../../styles/variables.scss';

.arrowRight{
    @media (min-width: 768px){
    display: grid;
    grid-template-columns: 40px 2px;
    grid-template-rows: auto auto;
    width: 92%;
    height: 1.5em;
    &__line {
        grid-area: 1/2/2/1;
        width: 160px;
        height: 110px;
        border-radius: 50%;
        border-left: solid 5px $third-color;
        transform: translateX(25px);
        }
    &__point{
        grid-column: 2;
        grid-row: 2;
        height: 30px;
        width: 30px;
        border-radius: 100%;
        border-right: solid 5px $third-color;
        transform: translateY(-20px) translateX(33px) rotate(0.08turn);
        }
    }
}